#section-cv
    & > .section-content
        font-size: 16rem

.resume-builder-controls
    font-size: 16rem

    display: flex
    flex-direction: row

    font-size: 16rem

    & > *
        padding: 5rem
        border: 1rem solid black
        &.selected
            background-color:#707070

#file-controls
    font-size: 16rem
    display: flex
    flex-direction: column
    & > *
        width: min-content
        white-space: nowrap


#display-info
    display: flex
    gap: 3em

    border: 1rem solid black
    padding: 20rem
    font-size: 20rem

    color: #2a2972

    & > div
        width: 100%

    .descr
        color: white
    .export-container
        text-align: right


#ss-named-cvs
    display: flex
    gap: 30rem

    #named-cvs-controls
        display: flex
        flex-direction: column
        justify-content: space-around
        & > *
            background-color: #2a2a2a
            color: #cecece
            width: 40rem
            text-align: center
            height: min-content
            &:hover
                background-color: #2a2a2a
                color: #e4e4e4
                cursor: pointer

.cv-thumnail-container
    width: 100%
    display: flex
    gap: 20rem
    overflow-x: scroll

    .cv-thumbnail
        flex: 1 0 75rem
        max-width: 75rem
        height: 100rem

        padding: 5rem

        word-wrap: break-word

        text-align: center
        align-content: center

        font-size: 10rem
        color: grey
        background-color: white

        border-radius: 7rem

        &:hover
            border: 2rem solid blue

        &.active
            color: black
            font-weight: bold
            border: 2rem solid black

#ss-export
    display: flex
    justify-content: center
    gap: 30rem
    button
        width: max-content
        padding: 5rem 10rem


// POPUPS --------------------------------

.popup-content
    padding: 20rem
    display: flex
    flex-direction: column
    align-items: center
    gap: 20rem
    & > *
        width: max-content